  background-color: #95b8d1;
  color: white;
  transition: all 0.6s ease; }
  #menu-slide .nav-close-wrapper {
    text-align: right; }
  #menu-slide .nav-btn-close {
    font-size: 2em; }
    #menu-slide .nav-btn-close:hover {
      color: rgba(255, 255, 255, 0.7); }
  #menu-slide .nav-list {
    list-style-type: none;
    margin-top: 1.5em;
    padding-left: 0.5em;
    font-size: 1.2em; }
    #menu-slide .nav-list .nav-item {
      margin-bottom: 0.5em; }
    #menu-slide .nav-list .nav-link {
      border-bottom: none;
      color: white; }
      #menu-slide .nav-list .nav-link:hover {
        color: rgba(255, 255, 255, 0.7); }

#menu-slide.menu-open {
  margin-right: 0; }

.menu-overlay {
  position: fixed;
  visibility: hidden;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0;
  transition: all 0.4s ease; }

.menu-open + .menu-overlay {
  visibility: visible;
